import React from "react";
import { Map, TileLayer} from "react-leaflet";
//import { Icon } from "leaflet";
import "./App.css";
import * as L from "leaflet";
const opencage = require('opencage-api-client');

const OCD_API_KEY = process.env.REACT_APP_OCD_API_KEY; 

  class App extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        input: ""
      };
    }
 
    
    updateInput(e) {
      this.setState({
        input: e.target.value
      });
    }



    // Adds marker to map and flies to it with an animation
    addLocation =() =>{
      opencage
        .geocode({ q: this.state.input, key: OCD_API_KEY})
        .then(data => {
          // Found at least one result
          if (data.results.length > 0){
              console.log("Found: " + data.results[0].formatted);
              const latlng = data.results[0].geometry;
              let mapInst =  this.refs.map.leafletElement;
              mapInst.flyTo(latlng, 15);
          } else alert("No results found!!");

        })
        .catch(error => {
          console.log('error', error.message);
        });


    }


    componentDidMount() {  

     let mapInst =  this.refs.map.leafletElement;

     (async () => {
      const stations = await fetch('https://features.byteroad.net/collections/gas_stations/items?limit=1065&f=json', {
        headers: {
          'Accept': 'application/geo+json'
        }
      }).then(response => response.json());
      const iconurl = 'data:image/svg+xml;base64,'+btoa('<?xml version="1.0" encoding="UTF-8" standalone="no"?><!-- Created with Inkscape (http://www.inkscape.org/) --><svg   xmlns:dc="http://purl.org/dc/elements/1.1/"   xmlns:cc="http://creativecommons.org/ns#"   xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#"   xmlns:svg="http://www.w3.org/2000/svg"   xmlns="http://www.w3.org/2000/svg"   xmlns:xlink="http://www.w3.org/1999/xlink"   xmlns:sodipodi="http://sodipodi.sourceforge.net/DTD/sodipodi-0.dtd"   xmlns:inkscape="http://www.inkscape.org/namespaces/inkscape"   width="135.20209mm"   height="135.20209mm"   viewBox="0 0 135.20209 135.20209"   version="1.1"   id="svg106"   inkscape:version="0.92.5 (2060ec1f9f, 2020-04-08)"   sodipodi:docname="icon2.svg">  <defs     id="defs100" />  <sodipodi:namedview     id="base"     pagecolor="#ffffff"     bordercolor="#666666"     borderopacity="1.0"     inkscape:pageopacity="0.0"     inkscape:pageshadow="2"     inkscape:zoom="0.35"     inkscape:cx="-410.21431"     inkscape:cy="555.49999"     inkscape:document-units="mm"     inkscape:current-layer="layer1"     showgrid="false"     inkscape:window-width="2592"     inkscape:window-height="1696"     inkscape:window-x="144"     inkscape:window-y="54"     inkscape:window-maximized="1" />  <metadata     id="metadata103">    <rdf:RDF>      <cc:Work         rdf:about="">        <dc:format>image/svg+xml</dc:format>        <dc:type           rdf:resource="http://purl.org/dc/dcmitype/StillImage" />        <dc:title></dc:title>      </cc:Work>    </rdf:RDF>  </metadata>  <g     inkscape:label="Layer 1"     inkscape:groupmode="layer"     id="layer1"     transform="translate(-214.3692,-160.60728)">    <image       y="160.60728"       x="214.3692"       id="image170"       xlink:href="data:image/png;base64,"       style="image-rendering:optimizeSpeed"       preserveAspectRatio="none"       height="135.20209"       width="135.20209" />  </g></svg>');
      
      L.geoJSON(stations, {
        pointToLayer: function (feature, latlng) {
          return L.marker(latlng, { 
            icon: L.icon({
              iconUrl: iconurl,
              iconSize: [40, 40],
              iconAnchor: [10, 10],
              popupAnchor: [0, -10],
            })
          });
        },
        onEachFeature: onEachFeature
      }).addTo(mapInst);
    })();
  
    function onEachFeature(feature, layer) {
      var popupContent = "<a href='https://features.byteroad.net/collections/gas_stations/items/" + feature.id + "' target='_blank'>" + feature.properties.Marca + "<br>" + feature.properties.Morada + "</a>";
      if (feature.properties && feature.properties.popupContent) {
        popupContent += feature.properties.popupContent;
      }
      layer.bindPopup(popupContent);
    }


    }


  
  render() {

  return (
    <div className="App mt-3">

      <h1 className="mytitle">IVAucher - Rede de Aderentes</h1>
      <div className="text-container">
        <p className="text-justify">
          Este mapa mostra a lista de postos de abastecimento de combustiveis aderentes ao programa <a href="https://www.ivaucher.pt/">IVAucher</a>. Foi criada, com base <a href="https://static.ivaucher.pt/docs/Lista-de-Postos-de-Abastecimento-de-Combustiveis-Aderentes_101121.pdf">nesta</a> lista.

          Os dados foram <a href="https://features.byteroad.net/">publicados</a> usando o standard <a href="https://opengeospatial.github.io/ogc-features-website/dist/">OGC API Features</a>, que permite partilhar dados geospaciais na web.
        </p>
      </div>

      <Map ref='map' center={[38.695927, -9.194453]}
        onClick={this.addMarker}
      zoom={10}
      >
        <TileLayer
          url="https://stamen-tiles-{s}.a.ssl.fastly.net/toner/{z}/{x}/{y}{r}.png"
          attribution='&copy; Map tiles by <a href="http://stamen.com">Stamen Design</a>, <a href="http://creativecommons.org/licenses/by/3.0">CC BY 3.0</a> &mdash; Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
      </Map>
      <br></br>
      <div className="container">
      <div className="form-inline mb-3">
          <input
            className="form-control flex-primary-1"
            onChange={e => this.updateInput(e)}
            value={this.state.input}
          />

          <button
            className="btn btn-primary ml-2"
            onClick={e => this.addLocation()}
          >
            Procurar
          </button>
        </div>
        </div>

        <a href="https://byteroad.net/">
          <img className="logo" src={require("./images/BR_logo_full_04.png")} alt={"ByteRoad"}/>
        </a>
   
    </div>
  );
}

  }

export default App;